@import '../../styles/colors.module.css';

.registration_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
  margin-bottom: 180px;
}

.registration_title {
  margin-top: 60px;
  margin-bottom: 40px;
  font-family: Manrope;
  font-size: 36px;
  font-weight: 500;
}

.registration_form {
  width: 608px;
  height: 617px;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--primary-white-color);
  box-shadow: var(--input-shadow);
}

.input {
  width: 440px;
  height: 46px;
  border: 1px solid var(--blue-color);
  border-radius: 40px;
  margin-bottom: 32px;
  padding-left: 16px;
  font-family: Manrope;
  font-size: 16px;
  font-weight: 400;
  outline: none;
}

.input.errorInput {
  border-color: var(--red-color);
}
.successInput {
  border-color: var(--green-color);
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.button {
  width: 456px;
  height: 46px;
  border: 1px solid var(--blue-color);
  background-color: var(--blue-color);
  color: #fff;
  border: none;
  border-radius: 40px;
  cursor: url('../../images/pawCursor.svg'), auto;
  margin-top: 8px;
  margin-bottom: 16px;
  font-family: Manrope;
  font-size: 20px;
  font-weight: 600;
  transition: transform 0.3s ease;
}
.button:hover {
  animation: pulse 1s infinite;
}

.refTitleToLogin {
  font-family: Manrope;
  font-size: 12px;
  font-weight: 200;
  color: #888888;
  margin-bottom: 60px;
}

.refLogin {
  font-family: Manrope;
  font-size: 12px;
  font-weight: 200;
  color: var(--blue-color);
  cursor: url('../../images/pawCursor.svg'), auto;
}

p {
  margin: 0;
  padding: 0;
}

.inputContainer {
  position: relative;
}

.error {
  color: var(--red-color);
  font-family: Manrope;
  font-size: 12px;
  font-weight: 400;
  position: absolute;
  bottom: 16px;
  left: 16px;
}

.successMessage {
  color: var(--green-color);
  font-family: Manrope;
  font-size: 12px;
  font-weight: 400;
  position: absolute;
  bottom: 16px;
  left: 16px;
}

.imageEye {
  position: absolute;
  top: 32%;
  right: 15px;
  transform: translateY(-50%);
  cursor: url('../../images/pawCursor.svg'), auto;
  transition: right 0.3s ease;
}

.imageCheck {
  height: 24px;
  position: absolute;
  top: 32%;
  right: 15px;
  transform: translateY(-50%);
  cursor: url('../../images/pawCursor.svg'), auto;
}

.imageCross {
  position: absolute;
  top: 32%;
  right: 15px;
  transform: translateY(-50%);
  cursor: url('../../images/pawCursor.svg'), auto;
}

@media (max-width: 767px) {
  .registration_form {
    width: 280px;
    height: 479px;
    border-radius: 20px;
  }
  .input {
    width: 240px;
    margin-bottom: 14px;
  }
  .button {
    width: 256px;
    height: 48px;
    margin-bottom: 8px;
  }
  .error {
    bottom: 0px;
  }
  .registration_title {
    margin-top: 40px;
    margin-bottom: 20px;
  }
  .refTitleToLogin {
    margin-bottom: 40px;
  }
  .imageEye {
    top: 39%;
  }
  .imageCross {
    top: 39%;
  }
  .imageCheck {
    top: 39%;
  }
  .successMessage {
    top: 50px;
  }
}
